.top-menu__link {
  display: inline-block;
  width: 60px;
  height: 32px;
  -webkit-transform: rotate(0);
  transform: rotate(0);
  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  position: relative;
  z-index: 14;
  position: fixed;
  right: 20px;
  top: 20px;
}
.top-menu__link span {
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  background: #f63;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0);
  transition: 0.25s ease-in-out;
}
.top-menu__link span:nth-child(1) {
  top: 0;
}
.top-menu__link span:nth-child(2) {
  top: 15px;
}
.top-menu__link span:nth-child(3) {
  top: 29px;
}
.top-menu__link.active span {
  background: #fff;
}
.top-menu__link.active span:nth-child(1) {
  top: 18px;
  transform: rotate(135deg);
}
.top-menu__link.active span:nth-child(2) {
  opacity: 0;
  left: -60px;
}
.top-menu__link.active span:nth-child(3) {
  top: 18px;
  transform: rotate(-135deg);
}

