
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {  
   
}

@media only screen and (-webkit-min-device-pixel-ratio: 0) {

}

//MEDIA
@media screen and (max-width: 1200px){
    .brand__info {
        padding-left: 50px;
    }
}

@media screen and (max-width: 1180px){
    .brand-view__inner {
        padding: 65px 50px;
        width: calc(100% - 40px);
    }
    .brand-view__close {
        top: 15px;
        right: 15px;
    }
    .brand-view__info {
        padding: 20px;
    }
    .brand-view__list {
        padding: 0 20px;
        margin-bottom: -30px;
    }
    .brand-view__wrap {
        margin: 0 -20px;
    }
    .brand-view__count {
        font-size: 55px;
        width: 110px;
    }
    .brand-view__item {
        margin-bottom: 30px;
    }
}

@media screen and (max-width: 1023px){
    .meet__item:nth-child(3) .meet__txt {
        padding-right: 30px;
    }
    .meet__partner {
        width: 100%;
    }
}

@media screen and (max-width: 991px){
    h1{
        font-size: 48px;
    }
    h3{
        font-size: 24px;
    }
    .item-line {
		font-size: 140px;
	}
    .brand{
        padding: 40px 0;
    }
    .brand__txt {
        font-size: 58px;
    }
    .brand-view__info {
        padding: 0;
        width: 100%;
        margin-bottom: 30px;
    }
    .brand-view__list {
        padding: 0;
        margin-bottom: 0;
        width: 100%;
    }
    .brand-view__wrap {
        margin: 0;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    }
    .brand-view__item {
        width: 33.333%;
    }
    .page-video {
        padding-bottom: 40px;
    }
    .page-video__view {
        height: 460px;
    }
    .meet{
        padding: 40px 0;
    }
    .meet__list {
        margin-bottom: 60px;
    }
    .meet h2 {
        font-size: 74px;
        margin-bottom: 80px;
    }
    .meet__txt {
        font-size: 20px;
        padding-right: 30px;
    }
    .meet__item:nth-child(2) .meet__txt {
        padding-right: 0;
    }
    .meet__item:nth-child(even) .meet__img {
        padding-right: 30px;
    }
    .meet__item {
        margin-bottom: 100px;
    }
    .meet__item:last-child {
        margin-bottom: 0;
    }
    .meet__item:nth-child(2) {
        margin-top: 0;
        margin-bottom: 100px;
    }
    .meet__item:nth-child(even) {
        -webkit-box-align: start;
        -ms-flex-align: start;
        align-items: flex-start;
    }
    .t-line {
        height: 30px;
    }
}
@media screen and (max-width: 767px) {
    body{
        font-size: 14px;
    }
    h1{
        font-size: 32px;
        margin-bottom: 20px;
    }
    h2{
        font-size: 24px;
        margin-bottom: 30px;
    }
    h3{
        font-size: 20px;
        margin-bottom: 20px;
    }
    h4{
        font-size: 18px;
    }
    .item-line {
		font-size: 88px;
	}
    
    .btn {
        padding: 12.5px 20px;
        font-size: 18px;
    }
    .page-header__info{
        display: none;
    }
    .page-footer{
        padding: 20px 0;
        font-size: 12px;
        p{
            margin: 0 0 15px 0;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
    .page-top {
        padding: 40px 0;
    }
    .page-footer {
        padding: 0 0 40px;
        font-size: 12px;
    }
    .page-footer__wrap {
        flex-wrap: wrap;
        padding-top: 40px;
        padding-bottom: 45px;
        position: relative;
    }
    .page-footer__l {
        margin-right: 0;
        width: 100%;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 30px;
    }
    .page-footer__info {
        text-align: center;
    }
    


    .brand-view__inner {
        width: calc(100% - 32px);
        padding: 60px 32px 32px;
    }
    .brand-view__top {
        font-size: 18px;
    }
    .brand-view__count {
        font-size: 34px;
        line-height: 1;
        width: calc(110px - 10px);
        border: 0;
    }
    .brand-view__item {
        margin-bottom: 20px;
    }
    .brand-view__info {
        margin-bottom: 20px;
    }
    .brand-view__list {
        margin-bottom: -20px;
    }
    .brand-view__close {
        width: 30px;
        height: 30px;
        background-size: cover;
    }
    .brand__info {
        padding-left: 0;
    }
    .brand__txt {
        font-size: 42px;
    }
    .page-video__view {
        height: 375px;
    }
    .meet h2 {
        font-size: 72px;
    }
    
}


@media screen and (max-width: 600px){
    input[type="text"]:-webkit-autofill {
        font-size: 12px!important;
    }
    input[type="text"]:-webkit-autofill,
    input[type="text"]:-webkit-autofill:hover,
    input[type="text"]:-webkit-autofill:focus,
    input[type="text"]:-webkit-autofill:active {
        font-size: 12px!important;
    }
    input:-webkit-autofill::first-line {
        font-size: 12px!important;
    }
    .p-disclaimer{
        font-size: 10px;
        line-height: 12px;
    }
    .page-header__wrap {
        justify-content: center;
        padding: 5px 0;
    }
    .item-player img{
        max-width: 150%;
        transform: translate(2%, 0)!important;
    }
    .page-menu {
        width: calc(100% - 20px);
    }
    .page-menu__item,
    .page-menu-external{
        padding: 0 30px;
    }
    .page-menu__list {
        padding: 100px 0 20px;
    }
    .page-menu__link{
        font-size: 36px;
    }
    .item-txt-wrap{
        bottom: 40px;
    }


    .item-bg{
        display: none;
    }
    .item-player__bg {
        height: 50%;
        background: linear-gradient(0deg, #000 100px, rgba(0, 0, 0, 0));
        z-index: 2;
    }
    .item-line__wrap{
        transform: none!important;
    }
    .item-txt-wrap,
    .item-player__bg{
        transform: none!important;
        opacity: 1!important;
    }
    .item-txt {
        max-width: unset;
        width: 100%;
        text-align: center;
    }
    .btn-wrap {
        max-width: unset;
        width: 100%;
        flex-direction: row;
        justify-content: center;
    }
    .btn{
        min-width: 240px;
        max-width: 340px;
    }
    .page-video {
        margin: 0 -20px;
    }
    .meet h2 {
        font-size: 50px;
    }
    .meet__item:nth-child(2) .meet-i__wrap {
        margin-bottom: 10px;
    }
    .meet__item:nth-child(2) .meet__inner .t-line {
        text-align: right;
    }
    .meet__txt {
        font-size: 15px;
        padding-right: 0;
        width: 100%;
        -webkit-box-ordinal-group: 3;
        -ms-flex-order: 2;
        order: 2;
    }
    .meet__item {
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    }
    .meet__img {
        width: 100%;
        display: block;
        margin-bottom: 16px;
        -webkit-box-ordinal-group: 2;
        -ms-flex-order: 1;
        order: 1;
    }
    .meet__item:nth-child(even) .meet__img {
        padding-right: 0;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: end;
        -ms-flex-pack: end;
        justify-content: flex-end;
    }
    .meet__item:nth-child(3) .meet__txt {
        padding-right: 0;
    }
    .meet__partner {
        padding: 20px;
        -webkit-box-shadow: rgba(149, 157, 165, 0.2) 0 8px 24px;
        box-shadow: rgba(149, 157, 165, 0.2) 0 8px 24px;
        margin-top: 20px;
    }
    .t-line__inner {
        width: 100%;
    }
    .t-line {
        height: 23px;
    }
    .meet {
        background: #000;
        position: relative;
    }
}