.top-menu__link {
    display: inline-block;
    width: 60px;
    height: 32px;
    -webkit-transform: rotate(0);
    transform: rotate(0);
    -webkit-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out;
    position: relative;
    z-index: 14;
    position: fixed;
    right: 20px;
    top: 20px;
    span {
        display: block;
        position: absolute;
        height: 3px;
        width: 100%;
        background: #f63;
        border-radius: 9px;
        opacity: 1;
        left: 0;
        transform: rotate(0);
        transition: 0.25s ease-in-out;
        &:nth-child(1){
            top: 0;
        }
        &:nth-child(2){
            top: 15px;
        }
        &:nth-child(3){
            top: 29px;
        }
    }
    &.active{
        span{
            background: #fff;
            &:nth-child(1){
                top: 18px;
                transform: rotate(135deg);
            }
            &:nth-child(2){
                opacity: 0;
                left: -60px;
            }
            &:nth-child(3){
                top: 18px;
                transform: rotate(-135deg);
            }
        }
    }
}


.page-menu {
    position: fixed;
    width: 480px;
    right: 0;
    top: 0;
    height: 100vh;
    pointer-events: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding-bottom: 53px;
    z-index: 12;
    &.active{
        pointer-events: all;
        .page-menu__overlay{
            opacity: 1;
            pointer-events: all;
        }
        .page-menu-external {
            pointer-events: all;
            opacity: 1;
            transition: all 0.3s ease 0.5s;
        }
    }
    &__bg{
        transform: translate(100%, 0);
        height: 100%;
        width: 100%;
        position: fixed;
        top: 0;
        background-color: #f63;
        will-change: transform;
    }
    &__overlay {
        position: fixed;
        background: rgba(0, 0, 0, 0.6);
        width: 100vw;
        height: 100vh;
        left: 0;
        top: 0;
        pointer-events: none;
        text-indent: -9999em;
        opacity: 0;
        transition: all .25s ease;
    }
    &__list{
        margin: 0;
        padding: 147px 0 20px;
        list-style-type: none;
    }
    &__item{
        transform: translate(100%, 0);
        margin-bottom: 10px;
        padding: 0 70px;
        cursor: pointer;
        will-change: transform;
        &:last-child{
            margin-bottom: 0;
        }
        &:before{
            display: none;
        }
    }
    &__link{
        color: #000;
        text-decoration: none;
        transition: color 0.25s ease;
        font-size: 56px;
        pointer-events: all;
        &:hover{
            color: #fff;
        }
    }
}


.page-menu-external {
    position: relative;
    padding: 0 70px;
    pointer-events: none;
    opacity: 0;
    transition: all 0s ease 0s;
    a{
        color: #000;
        text-decoration: underline;
        &:hover{
            text-decoration: none;
        }
    }
}



.brand {
    padding: 100px 0 110px;
}
.brand .container {
    max-width: 1270px;
}
.brand__info {
    padding-left: 150px;
}
.brand__pre {
    text-transform: uppercase;
    margin-bottom: 17px;
    font-size: 24px;
}
.brand__txt {
    font-size: 72px;
    line-height: 1.15;
    margin-bottom: 15px;
}
.brand__txt span {
    color: #f63;
}
.brand__link {
    display: inline-flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    color: #fff;
    text-decoration: none;
}
.brand__link:before {
    content: "";
    display: inline-block;
    width: 32px;
    height: 32px;
    border: 1px solid #999;
    border-radius: 50%;
    margin-right: 16px;
    background: url(../img/svg/plus.svg) center no-repeat;
}



.brand-view{
    color: #000;
    &.active{
        .brand-view__inner{
            top: 50%;
        }
        .brand-view__overlay{
            opacity: 1;
        }
    }
    &__inner{
        z-index: 20;
        background: #fff;
        padding: 120px 115px;
        width: 1144px;
        margin: 0 auto;
        position: fixed;
        top: 150vh;
        transition: all 0.4s ease-in-out;
        left: 50%;
        transform: translate(-50%, -50%);
        max-height: calc(100vh - 30px);
        overflow-y: auto;
    }
    &__overlay{
        position: fixed;
        background: rgba(0, 0, 0, 0.6);
        width: 100vw;
        height: 100vh;
        left: 0;
        top: 0;
        opacity: 0;
        z-index: 15;
        pointer-events: none;
        transition: opacity .25s ease;
    }
    &__close{
        display: inline-block;
        width: 43px;
        height: 43px;
        background: url(../img/svg/b-close.svg) center no-repeat;
        -webkit-transition: all 0.25s ease;
        transition: all 0.25s ease;
        position: absolute;
        top: 32px;
        right: 32px;
        &:hover{
            background: url(../img/svg/b-close-h.svg) center no-repeat;
        }
    }
    &__wrap{
        display: flex;
        margin: 0 -25px;
    }
    &__info{
        width: 50%;
        padding: 0 55px 0 25px;
    }
    &__top{
        font-size: 24px;
        margin-bottom: 30px;
    }
    &__list{
        width: 50%;
        padding: 0 25px;
        display: flex;
        flex-wrap: wrap;
        margin-bottom: -65px;
    }
    &__item{
        width: 50%;
        padding: 0 10px 0 0;
        margin-bottom: 65px;
    }
    &__count{
        color: #f63;
        font-size: 72px;
        line-height: 60px;
        width: 158px;
        margin-bottom: 6px;
    }
    &__add {
        font-size: 12px;
        color: #777;
    }
    &__txt {
        line-height: 1.5;
        strong{
            color: #f63;
        }
        a{
            color: #f63;
            text-decoration: underline;
            &:hover{
                text-decoration: none;
            }
        }
    }
}



